.editablePlantCard {
    display: flex;
    flex-direction: column;
    gap: 8px; /* Adjust spacing between fields */
  }
  
  .editablePlantCard label {
    margin-bottom: 4px; /* Space between label and input */
    font-weight: bold; /* Make labels stand out */
  }
  
  .editablePlantCard input,
  .editablePlantCard textarea {
    padding: 8px; /* Add some padding inside inputs and textareas */
    border: 1px solid #ccc; /* Light border for inputs */
    border-radius: 4px; /* Rounded corners */
  }
  
  .editablePlantCard .buttons {
    display: flex;
    gap: 10px; /* Space between buttons */
    margin-top: 20px; /* Space above the button section */
  }
  
  .editablePlantCard button {
    padding: 10px 20px; /* Button padding */
    border: none; /* Remove border */
    border-radius: 4px; /* Rounded corners */
    cursor: pointer; /* Pointer cursor on hover */
    background-color: #007bff; /* Example button color */
    color: white; /* Text color */
  }
  
  .editablePlantCard button:hover {
    background-color: #0056b3; /* Darker shade on hover */
  }
  