/* PlantDetailPage.css */
.detail-container {
    display: flex;
    flex-direction: column;
    max-width: 80vw; /* Adjust based on your design */
    margin: auto;
  }
  
  .detail-field {
    display: flex;
    align-items: center; /* Align items vertically */
    justify-content: space-between; /* Space between label and field */
    margin-bottom: 16px;
    text-align: left;
  }
  
  .detail-label {
    font-weight: bold;
    margin-bottom: 4px;
    flex-basis: 20%;
    text-align: left;
  }
  
  .detail-value, .detail-edit-input, .detail-edit-textarea {
    flex-grow: 1; /* Allow input and text areas to fill the available space */
    margin-left: 16px; /* Space between label and field */
  }
  .detail-edit-input, .detail-edit-textarea {
    width: 100%; /* Ensure inputs fill their container */
    box-sizing: border-box; /* Include padding and border in width */
  }
  
  .detail-edit-textarea {
    height: 100px; /* Adjust height as needed */
  }

  .text-red {
    color: red;
  }

  .image {
    width: 100%;
    max-width: 600px;
    margin: 16px auto;
  }