.Menu-options {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 20;
  left: 0;
  background-color: white;
  border: 1px solid #ddd;
  z-index: 1000;
}

.Menu-options a {
  padding: 8px 12px;
  text-decoration: none;
  color: black;
  border-bottom: 1px solid #ddd;
}

.Menu-options a:hover {
  background-color: #f5f5f5;
}

.Menu-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 8px;
  }
  
.Hamburger-menu {
    font-size: 24px;
    background: none;
    border: none;
    cursor: pointer;
    padding-right: 16px;
  }
  
.Hamburger-menu:focus {
    outline: none;
  }